@use "base/variables" as v;
@use "base/mixins" as m;

.esthetics-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$primaryBlueDark;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.esthetics-description {
  .esthetics-description-grid {
    display: grid;
    gap: 1rem;
    @include m.laptop {
      grid-template-columns: 1fr 3fr;
      gap: 3rem;
    }
    .esthetics-description-logo {
      display: flex;
      align-items: center;
      width: 100%;
      img {
        width: 100%;
      }
    }
    .esthetics-description-text {
      display: flex;
      align-items: center;
      padding: 2rem;
      p {
        text-align: justify;
        // font-size: 2rem;
      }
    }
  }
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$white;
  p {
    color: v.$secondaryGray;
    margin: 0;
    font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
  }
}

.esthetics-elements {
  .esthetics-element-odd {
    .esthetics-element-title {
      background-color: v.$secondaryBlue;
      padding: 2.5rem 5rem 2.5rem 0;
      text-align: end;
      border-radius: 0 5rem 5rem 0;
    }
    .esthetics-element-description {
      padding: 7rem 3rem 4rem 1rem;
      @include m.laptop {
        margin-left: 20%;
        padding: 7rem 15rem 4rem 0;
      }
      @include m.desktop {
        padding: 12rem 3rem 10rem 1rem;
      }
    }
    .esthetics-element-image {
      right: -7.5rem;
    }
  }
  //  esthetics-element-short

  .esthetics-element-odd:not(.esthetics-element-short),
  .esthetics-element-pair:not(.esthetics-element-short) {
    margin-bottom: 30rem;
    @include m.tablet {
      margin-bottom: 45rem;
    }
    @include m.desktop {
      margin-bottom: 60rem;
    }
  }

  .esthetics-element-short {
    margin-bottom: 20rem;
    @include m.tablet {
      margin-bottom: 30rem;
    }
    @include m.laptop {
      margin-bottom: 40rem;
    }
    @include m.desktop {
      margin-bottom: 45rem;
    }
    .esthetics-element-description {
      // @include m.desktop {
      //   padding: 15rem 3rem 1rem;
      // }
    }
  }

  .esthetics-element-title {
    font-size: 1.5rem;
    color: v.$white;
    width: 90%;
    position: relative;
    @include m.mobile {
      font-size: 1.75rem;
    }
    @include m.tablet {
      font-size: 1.8rem;
      width: 75%;
    }
    @include m.laptop {
      font-size: 2.5rem;
      width: 60%;
    }
    @include m.desktop {
      font-size: 3rem;
      width: 50%;
    }
    span {
      display: none;
      @include m.tablet {
        display: inline;
      }
    }
  }

  .esthetics-element-description {
    background-color: v.$grayBackground;
    margin-left: 0;
    position: absolute;
    left: 0;
    top: 3.5rem;
    width: 100%;
    z-index: -1;
    padding: 5rem 3rem 1rem;
    text-align: center;
    color: v.$secondaryGray;
    font-size: 1.5rem;
    line-height: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include m.tablet {
      // left: 2.5rem;
      // height: 40rem;
      // width: 40rem;
      font-size: 2rem;
      // padding: 7rem 3rem 4rem 1rem;
    }
    @include m.laptop {
      // width: 98%;
    }
    @include m.desktop {
      // padding: 8rem 3rem 1rem;
    }
    ul {
      list-style-position: inside;
    }
  }

  .esthetics-element-image {
    position: absolute;
    bottom: -10rem;
    width: 20rem;
    @include m.laptop {
      width: 30rem;
    }
    img {
      width: 100%;
      display: none;
      @include m.tablet {
        display: block;
      }
    }
  }

  .esthetics-element-pair {
    .esthetics-element-title {
      background-color: v.$primaryBlueDark;
      padding: 2.5rem 0 2.5rem 5rem;
      text-align: start;
      border-radius: 5rem 0 0 5rem;
      margin-left: 10%;
      @include m.tablet {
        margin-left: 25%;
      }
      @include m.laptop{
        margin-left: 40%;
      }
      @include m.laptop{
        margin-left: 50%;
      }
    }
    .esthetics-element-description {
      padding: 7rem 1rem 4rem 3rem;
      @include m.laptop {
        position: absolute;
        padding: 7rem 0 4rem 15rem;
        left: unset;
        right: 10%;
      }
      @include m.desktop {
        padding: 12rem 3rem 10rem 1rem;
      }
    }
    .esthetics-element-image {
      left: -7.5rem;
      // @include m.laptop {
      //   display: none; //////////////////////
      // }
    }
  }
  margin-bottom: 40rem;
  @include m.tablet {
    margin-bottom: 60rem;
  }
  @include m.desktop {
    margin-bottom: 100rem;
  }
}
