@use "base/variables" as v;
@use "base/mixins" as m;

.lab-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$white;
    font-size: 2.5rem;
    top: -1.5rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.lab-description {
  .lab-description-grid {
    display: grid;
    gap: 1rem;
    @include m.laptop {
      grid-template-columns: 1fr 3fr;
      gap: 3rem;
    }
    .lab-description-logo {
      display: flex;
      align-items: center;
      width: 17rem;
      margin: auto;
      @include m.tablet {
        width: 30rem;
      }
      img {
        width: 100%;
      }
    }
    .lab-description-text {
      display: flex;
      align-items: center;
      font-family: v.$text;
      padding: 2rem;
      p {
        text-align: justify;
        // font-size: 2rem;
        line-height: 1.5;
        @include m.mobile {
          line-height: unset;
        }
      }
    }
  }
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$primaryBlueDark;
  p {
    color: v.$white;
    margin: 0;
    font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
  }
}

.lab-procedures {
  background-color: v.$grayBackground;
  margin: 3rem 0;
  padding: 3rem;
  @include m.tablet {
    margin: 3rem;
  }
  @include m.desktop {
    width: 90%;
    margin: 3rem auto;
  }
  @include m.desktopXL {
    max-width: 120rem;
    margin: 3rem auto;
  }
  p,
  li {
    // font-size: 1.5rem;
    font-family: v.$title;
  }
  li {
    color: v.$primaryBlue;
  }
  ul {
    p {
      margin-left: -2rem;
      margin-right: -1rem;
    }
  }
}

.lab-procedures-list {
  // margin: 5rem;
  height: 10rem;
  @include m.tablet {
    height: 15rem;
    margin-top: 5rem;
  }
  @include m.laptop {
    height: 20rem;
  }
  @include m.desktop {
    height: 30rem;
  }
  .lab-procedures-list-head {
    position: relative;
    h2 {
      text-transform: uppercase;
      position: absolute;
      top: 1rem;
      font-size: 2rem;
      text-align: center;
      width: 100%;
      @include m.tablet {
        width: 70%;
        font-size: 3rem;
        margin-left: 4.5rem;
        text-align: left;
      }
      @include m.laptop {
        width: 60%;
        margin-left: 12.5rem;
      }
      @include m.desktop {
        width: 60%;
        margin-left: 12.5rem;
        font-size: 4.5rem;
      }
      @include m.desktopXL {
        width: 60%;
        margin-left: 22.5%;
        font-size: 5rem;
      }
      .lab-head-2 {
        display: block;
        z-index: 3;
        color: v.$white;
      }
    }
    .bottom-area {
      z-index: -1;
      height: 7.5rem;
      width: 100%;
      margin: auto;
      background-color: v.$primaryBlue;
      position: absolute;
      top: 3.5rem;
      border-radius: 0 0 0 0;
      @include m.tablet {
        width: 85%;
        top: 4.75rem;
        height: 7.5rem;
        margin-left: 3rem;
        border-radius: 0 0 0 5rem;
      }
      @include m.laptop {
        width: 72.5%;
        margin-left: 9rem;
        height: 10rem;
        top: 4.5rem;
      }
      @include m.desktop {
        width: 80%;
        top: 6.5rem;
        height: 15rem;
        border-radius: 0 0 0 10rem;
      }
      @include m.desktopXL {
        width: 50%;
        top: 6.5rem;
        margin-left: 20%;
      }
    }
    .lab-head-logo {
      display: none;
      position: absolute;
      right: 5rem;
      top: 2rem;
      padding: 2rem;
      background-color: v.$grayBackground;
      width: 20rem;
      height: 20rem;
      border-radius: 50% 50% 0 0;
      @include m.tablet {
        display: block;
        width: 10rem;
        height: 10rem;
        top: -2rem;
        right: 3rem;
      }
      @include m.laptop {
        width: 12.5rem;
        height: 12.5rem;
        right: 10rem;
      }
      @include m.desktop {
        width: 15rem;
        height: 15rem;
        right: 10rem;
      }
      @include m.desktopXL {
        width: 17.5rem;
        height: 17.5rem;
        right: 25%;
      }
      img {
        width: 100%;
        padding: 2rem 3rem 4rem 3rem;
        @include m.tablet {
          padding: 0 0.5rem 1rem 0.5rem;
        }
        @include m.laptop {
          padding: 0 1rem 2rem 1rem;
        }
        @include m.desktop {
          padding: 1.5rem 2rem 1rem 2rem;
        }
      }
    }
  }
}

// Pagination

.lab-pagination {
  max-width: 120rem;
  margin: auto;
  margin-bottom: 5rem;
  @include m.tablet {
    margin-bottom: 10rem;
  }
  .panel {
    display: flex;
    justify-content: end;
    margin: 2rem;
    font-size: 1.25rem;
    @include m.mobile {
      margin: 5rem 2rem 3rem 2rem;
    }
    @include m.tablet {
      width: 70%;
      margin: 0 auto 3rem auto;
    }
    @include m.desktop {
      width: 60%;
    }
    @include m.desktopXL {
      width: 50%;
    }
  }
  .input-group {
    display: flex;
    justify-content: center;
    @include m.mobile {
      flex-wrap: nowrap;
    }
    label {
      text-transform: uppercase;
      padding: 1rem 2rem 1rem 1rem;
    }
    input {
      border: 1px solid v.$primaryBlueDark;
      padding: 1rem;
      width: 100%;
      margin-top: 0.75rem;
      height: 3rem;
      @include m.mobile {
        padding: 0.5rem;
      }
      @include m.tablet {
        width: 20rem;
      }
    }
  }
  .lab-column {
    padding-left: 0;
    vertical-align: sub;
    @include m.tablet {
      border-left: 2px solid v.$primaryBlueDark;
    }
  }
  tr {
    border-style: hidden;
    height: 4rem;
    vertical-align: middle;
  }
  .lab-price {
    border-style: hidden;
    display: flex;
    justify-content: space-between;
    @include m.tablet {
      justify-content: space-between;
      width: 7.5rem;
    }
  }
  .paginate_controls {
    display: flex;
    justify-content: center;
    margin: 3rem;
    input[type="button"]:not(.paginate_control_prev, .paginate_control_next) {
      border-radius: 50%;
      width: 2.5rem;
      height: 2.5rem;
      margin: 1rem 0.5rem 0.5rem 0.5rem;
      font-size: 1.5rem;
      border: 2px solid v.$primaryBlueDark;
      &:hover {
        background-color: v.$primaryBlueDark;
        color: v.$white;
      }
      @include m.tablet {
        width: 3.5rem;
        height: 3.5rem;
      }
    }
    input[type="button"]:not(.active) {
      background-color: transparent;
    }
    .active {
      background-color: v.$primaryBlueDark;
      color: v.$white;
    }
    .paginate_control_prev,
    .paginate_control_next {
      background-color: transparent;
      border: none;
      font-size: 2rem;
      color: v.$primaryBlueDark;
      @include m.tablet {
        font-size: 4rem;
      }
    }
  }
}
